* {
    padding: 0;
    margin: 0;
    list-style: none
}

main {
    background: #f5f5f5;
}

.main {
    width: 1214px;
    margin: 0 auto;
    padding-top: 5px;
    overflow: hidden;
}

.btnzsong {
    margin: 5px auto -10px;
    button {
        width: 71px;
        height: 34px;
        border: 1px solid #ccc;
    }
    .qie {
        background: #53aa5b;
        color: #fff;
        border: none;
    }
}

