.header-Dl .qieactive {
    background: #53aa5b!important;
}

.list {
    width: 1214px;
    margin: 0 auto;
    font-size: 14px;
}

.list ul {
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
    flex-wrap: wrap;
}

.list li {
    width: 220px;
    padding: 10px;
    // float: left;
    // margin: 0 22px 10px 0;
    margin-top: 20px;
    border: 1px solid #e6e6e6;
    background-color: #FFF;
    height: 310px;
    position: relative
}

.list li p {
    width: 200px;
    height: 48px;
    font-size: 14px;
    color: #444;
    line-height: 24px;
    overflow: hidden
}

.list li img {
    width: 180px;
    height: 180px;
    display: block
}

.list li span {
    display: block;
    margin-top: 5px;
}

.price {
    font-weight: 700;
    color: #F60;
}

.kucun {
    color: #999;
}

.m-style .active {
    float: left;
    margin: 0 5px;
    width: 38px;
    height: 38px;
    line-height: 38px;
    background: #53aa5b;
    color: #fff;
    font-size: 14px;
    border: 1px solid #53aa5b;
}

.page {
    margin: 10px -47px 0px 0;
}